import React, { useState } from "react";
import Styles from "./Login.module.scss";
import Box from "@mui/material/Box";
import {
  TextField,
  Button,
  IconButton,
  InputAdornment,
} from "@mui/material";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import { ErrorBoundary } from "react-error-boundary";
import ErrorComponent from "../../Component/Error/Error";
import { Apis } from "../../APIs/API";
const apis = new Apis("/");

export default function Login({ toggleHandler }) {
  const [showPassword, setShowPassword] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log(data, "DATATA")
    toggleHandler();
  };

  const handleClickShowPassword = () => setShowPassword(!showPassword);

  const loginComponent = () => {
    return (
      <Box
        className={Styles.loginBox}
        sx={{
          boxShadow: "-1px 6px 16px -1px rgba(75,74,100,0.55)",
        }}
      >
        <h1>Super Admin Login</h1>
        <h4 >Hey, Enter your details to get log in to your account.</h4>
        <Box
          component="form"
          onSubmit={handleSubmit}
          noValidate
          className={Styles.loginForm}
        >
          <TextField
            margin="none"
            labelid="email"
            required
            fullWidth
            id="email"
            label="Username"
            name="email"
            autoComplete="email"
            autoFocus
            sx={{
              "& input, fieldset": {
                borderRadius: "8.5px",
              },
            }}
          />
          <TextField
            margin="none"
            labelid="password"
            required
            fullWidth
            name="password"
            label="Password"
            type={showPassword ? "text" : "password"}
            id="password"
            autoComplete="password"
            sx={{
              "&": {
                "& input, fieldset, div": {
                  borderRadius: "8.5px",
                },
              },
            }}
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={handleClickShowPassword}
                    sx={{
                      backgroundColor: "inherit",
                    }}
                  >
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              ),
            }}
          />
          <Button
            sx={{
              "&": {
                height: "3em",
                width: "50%",
                borderRadius: "8.5px",
                fontFamily: "inherit",
                backgroundColor: "#3b38a2",
                marginTop: "10px",
                paddingTop: "10px",
                fontWeight: "400",
              },
              "&:hover": {
                backgroundColor: "#0d0b3b",
              },
            }}
            type="submit"
            variant="contained"
            fullWidth
          >
            Log In
          </Button>
        </Box>
      </Box>
    );
  };

  return (
    <div className={Styles.login}>
      <ErrorBoundary fallback={<ErrorComponent error="Could not load data" />}><>        
        {loginComponent()}
        </>
      </ErrorBoundary>
    </div>
  );
}
