

export class Apis {
     constructor(url) {
        this._url = url;
     }


     async login (email = String, password = String, prefix = String) {
             try {
                const userLogin = {
                    email,
                    password
                }
                const response = await fetch(this._url+prefix, {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json"
                    },
                    body: JSON.stringify(userLogin)
                });
                const responseData = await response.json();
                if(!responseData) return {};
                return responseData; 
             } catch (error) {
                throw new Error(error)
             }
     }

}